// Макет API функцій
export const mockAPI_UA = {
  // Подання заявки
  submitApplication: async (applicationData) => {
    return new Promise((resolve) => {
      setTimeout(() => {
        const application = {
          id: Date.now(),
          ...applicationData,
          submittedAt: new Date().toISOString(),
          status: 'pending'
        };
        mockData_UA.applications.push(application);
        resolve({
          success: true,
          message: "Заявку успішно подано! Ми зв'яжемося з вами протягом 2-3 робочих днів.",
          applicationId: application.id
        });
      }, 1000);
    });
  },

  // Отримання даних FAQ
  getFAQs: async () => {
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve({
          success: true,
          data: mockData_UA.faqs
        });
      }, 300);
    });
  },

  // Подання контактної форми
  submitContact: async (contactData) => {
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve({
          success: true,
          message: "Дякуємо за ваше повідомлення! Ми відповімо вам протягом 24 годин."
        });
      }, 800);
    });
  }
};